@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,500;0,700;0,900;1,400;1,500;1,700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
	font-family: 'Roboto', sans-serif;
}

.mainContent {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 0.8 1;
	        flex: 0.8 1;
}

.searchArea {
	display: -webkit-flex;
	display: flex;
}
.searchArea > SideBar {
	-webkit-flex: 0.2 1;
	        flex: 0.2 1;
}
.searchArea > SearchPage {
	-webkit-flex: 0.8 1;
	        flex: 0.8 1;
}

.header {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	padding: 5px 20px;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 100;
	background-color: #fff;
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
.Icons {
	cursor: pointer;
	width: 2rem;
	height: 2rem;
	font-size: 2rem;
}
.logo {
	height: 30px;
	object-fit: cover;
	margin-left: 25px;
}

.header__middle {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	width: 50%;
	border: 1px solid #ebdbdb;
	border-radius: 50px;
	overflow: hidden;
}

.header__middle > input {
	-webkit-flex: 1 1;
	        flex: 1 1;
	border: none;
	height: 100%;
	width: 100%;
	padding: 1rem;
	outline: none;
	overflow: hidden;
}
.search__button {
	width: 50px !important;
	height: 100%;
	background-color: #fafafa;
	border-left: 1px solid lightgray;
	color: gray;
}

.header__right {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.header__right > svg {
	margin-right: 5px;
}

.sidebar {
	padding: 5px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-flex: 0.2 1;
	        flex: 0.2 1;
	max-height: 93.5vh;
	height: 100%;
	min-width: 200px;
	max-width: 250px;
	/* border-right: 1px solid #bebebe; */
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	left: 0;
	z-index: 10;
	/* box-shadow: 0px 1px 5px #7c7c7c; */
	overflow-y: scroll;
}
hr {
	color: aqua;
	background-color: aquamarine;
	border: none;
	border-bottom: 0.2px solid #acacac;
	width: 90%;
	margin: 5px auto;
}

h4 {
	margin: 5px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
/* width */
::-webkit-scrollbar {
	width: 11px;
}

/* Track */
::-webkit-scrollbar-track {
	box-shadow: inset 0 0 1px #a5a5a5;
	border-radius: 10px;
}

::-webkit-scrollbar-thumb {
	background: #d1cdcd;
	border-radius: 10px;
}

/* Handle */

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: #b6b6b6;
}
.FirstButton {
	background-color: #a1a1a1;
}

.sidebar__buttons {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	height: 45px;
	padding: 8px;
	margin: 3px 0;
	/* background-color: #ffffff; */
	-webkit-align-items: center;
	        align-items: center;
	color: #353030;
	cursor: pointer;
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
.sidebar__buttons > span {
	-webkit-align-items: center;
	        align-items: center;
	font-size: 1rem;
	font-weight: 400;
	margin-top: 3px;
	margin-left: 15px;
	color: #222222;
}

.sidebar__buttons:hover {
	background-color: lightgray;
	border-radius: 2px;
}
.sidebar__buttons:hover > svg {
	color: #a21ab4;
}
.sidebar__buttons:hover > span {
	color: #000000;
}

.video__card {
	height: 300px;
	width: 340px;
	min-height: 250px;
	max-height: 300px;
	min-width: 270px;
	max-width: 340px;
	position: relative;
	cursor: pointer;
	border: 1px solid #ffffff;
	margin: 10px;
	border-radius: 3px;
	overflow: hidden;
}
.video__card > img {
	height: 190px;
	width: 340px;
	object-fit: cover;
}
.videocard__avatar {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	margin: 5px;
	-webkit-align-items: center;
	        align-items: center;
}
.videocard__info {
	display: -webkit-flex;
	display: flex;
	margin-top: 0;
}
.time {
	color: white;
	position: absolute;
	right: 5px;
	background-color: #00000067;
	padding: 2px;
	bottom: 115px;
	border-radius: 3px;
}
.video__text {
	margin-left: 10px;
}
.video__text > h4 {
	text-align: left;
}
.video__text > p {
	padding-top: 1px;
	padding-left: 5px;
}

.video__card:hover {
	box-shadow: 0 0 5px #ff9393;
}

.content {
	-webkit-flex: 0.8 1;
	        flex: 0.8 1;
	height: 100%;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	margin: auto;
}

.searchPage {
	/* display: flex; */
	-webkit-flex: 0.8 1;
	        flex: 0.8 1;
	padding: 15px;
}
.filterArea {
	cursor: pointer;
}
.filterArea > h3 {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	color: #2e2e2e;
}
.filterArea > h3 > span {
	margin-left: 10px;
}
.searchPage > hr {
	width: auto;
	border: 0;
	border-bottom: 1px solid #eb8167;
}

.searchItems {
	display: -webkit-flex;
	display: flex;
	height: 170px;
	width: auto;
	max-width: 1000px;
	margin: 15px;
	cursor: pointer;
}
.searchItems > img {
	height: 170px;
	width: 300px;
	object-fit: cover;
}
.video__info {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-left: 15px;
}
.video__info > h3 {
	margin: 15px 0;
}
.desc {
	margin-top: 5px;
	font-size: 0.7rem;
}
.Totalsubs {
	color: #2007ff;
}
.subSpan {
	background: linear-gradient(to right, #ffe7e7, #53dff8);
}

